import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Icon } from 'antd';
import axios from 'axios';    //ajax
import RouteTabComponent from '../../../component/routeTab/routeTab';  //tabs
import '../../../component/apis';

class BuyAdmins extends Component {
  constructor() {
    super();
    this.state = {
      loading: true,
      taobao: false,
      pinduoduo: false,
      jingdong: false
    }
  }

  componentWillMount() {
    // ajax 
    axios.get(global.constants.website+'/api/index/tbBind',{headers: {AppAuthorization: localStorage.getItem("token")}})   //
    .then(response => {
      let responses = response.data.data;
      // console.log(response.data.data);
      // 
      this.setState({
        datas_Status: response.data.data,                    // 

        jd_bind: responses.jd_bind.nickname,                 //
        jd_status: responses.jd_bind.bind_status,            //  
        jd_remark: responses.jd_bind.remark,                 //
        jd__status: responses.jd_bind.status,                // 
        pdd_bind: responses.pdd_bind.nickname,               //
        pdd_status: responses.pdd_bind.bind_status,          // 
        pdd_remark: responses.pdd_bind.remark,               //
        pdd__status: responses.pdd_bind.status,              // 
        taobao_bind: responses.taobao_bind.nickname,         //
        taobao_status: responses.taobao_bind.bind_status,    //  
        taobao_remark: responses.taobao_bind.remark,         //
        taobao__status: responses.taobao_bind.status,        //  
        wph_bind: responses.wph_bind.nickname,               //
        wph_status: responses.wph_bind.bind_status,          //  
        wph_remark: responses.wph_bind.remark,               //
        wph__status: responses.wph_bind.status,              //  
      })
    })
    .catch(error => {
      console.log(error);
    });
  }

  //  
  taobaoInto = () => {
    // json
    let datas_Status = this.state.datas_Status;
    // console.log(datas_Status);
    if ( this.state.taobao__status === 0 ) {
      this.props.history.push("/taobao")
    } else if ( this.state.taobao__status === 3 ) {
      this.props.history.push({pathname: "/correct_taobao", state: {data: datas_Status.taobao_bind.id}})
    } else {
      // console.log(123);
    }
  }

  //  
  pinduoduoInto = () => {
    // json
    let datas_Status = this.state.datas_Status;
    if ( this.state.pdd__status === 0 ) {
      this.props.history.push("/pinduoduo")
    } else if ( this.state.pdd__status === 3 ) {
      this.props.history.push({pathname: "/correct_pinduoduo", state: {data: datas_Status.pdd_bind.id}})
    } else {
      // console.log(123);
    }
  }

  //  
  jingdongInto = () => {
    // json
    let datas_Status = this.state.datas_Status;
    if ( this.state.jd__status === 0 ) {
      this.props.history.push("/jingdong")
    } else if ( this.state.jd__status === 3 ) {
      this.props.history.push({pathname: "/correct_jingdong", state: {data: datas_Status.jd_bind.id}})
    } else {
      // console.log(123);
    }
  }

  //  
  weipinhuiInto = () => {
    // json
    let datas_Status = this.state.datas_Status;
    if ( this.state.wph__status === 0 ) {
      this.props.history.push("/weipinhui")
    } else if ( this.state.wph__status === 3 ) {
      this.props.history.push({pathname: "/correct_weipinhui", state: {data: datas_Status.wph_bind.id}})
    } else {
      // console.log(123);
    }
    // console.log(this);
  }

  render() {
    const { taobao__status,pdd__status,wph__status,jd__status,jd_bind, jd_status, jd_remark, pdd_bind, pdd_status, pdd_remark, taobao_bind, taobao_status, taobao_remark, wph_bind, wph_status, wph_remark } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/myCenter"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <RouteTabComponent />
        <div className="buyAdmin-box">
          <ul>
            {/*  */}
            <li>
              {/* <Link to="/taobao"> */}
              {/* <div className="bind-list" onClick={ ()=>this.taobaoInto(item) }> */}
              <div className="bind-list" onClick={ this.taobaoInto }>
                <div><img src={require("../../../img/taobao.png")} alt=""/><span>{ taobao_bind }</span></div>
                <div><span>{ taobao_status }</span><img src={require("../../../img/jinru.png")} alt=""/></div>
              </div>
              {
                taobao__status === 3 ?
                  <p className="bind-content">{ taobao_remark }</p>
                :
                  ""
              }
              {/* </Link> */}
            </li>
            {/*  */}
            <li>
              <div className="bind-list" onClick={ this.pinduoduoInto }>
                <div><img src={require("../../../img/pinduoduo.png")} alt=""/><span>{ pdd_bind }</span></div>
                <div><span>{ pdd_status }</span><img src={require("../../../img/jinru.png")} alt=""/></div>
              </div>
              {
                pdd__status === 3 ?
                  <p className="bind-content">{ pdd_remark }</p>
                :
                ""
              }
            </li>
            {/*  */}
            <li>
              <div className="bind-list" onClick={ this.jingdongInto }>
                <div><img src={require("../../../img/jingdong1.png")} alt=""/><span>{ jd_bind }</span></div>
                <div><span>{ jd_status }</span><img src={require("../../../img/jinru.png")} alt=""/></div>
              </div>
              {
                jd__status === 3 ?
                  <p className="bind-content">{ jd_remark }</p>
                :
                ""
              }
            </li>
            {/*  */}
            <li>
              <div className="bind-list" onClick={ this.weipinhuiInto }>
                <div><img src={require("../../../img/weipinhui.png")} alt=""/><span>{ wph_bind }</span></div>
                <div><span>{ wph_status }</span><img src={require("../../../img/jinru.png")} alt=""/></div>
              </div>
              {
                wph__status === 3 ?
                  <p className="bind-content">{ wph_remark }</p>
                :
                  ""
              }
            </li>
          </ul>
        </div>
      </div>
    )
  }
}

export default BuyAdmins
